import React, { useState, useEffect } from "react";
import { ArrowLeft, ShieldCheck, Check, Loader2 } from "lucide-react";
import "../../styles/dashboard.css";
import { useLanguage } from "../../i18n/LanguageContext";
import * as api from "../../services/api";
import { CreditPackage } from "../../types/schema";
import { UniversalLink as Link } from "../../database/UniversalLink";
import { useUniversalRouter } from "../../database/routerViteGatsbyUniversal";
import { useUniversalSearchParams } from "../../database/useUniversalSearchParams";

export function CheckoutPage() {
  const { go } = useUniversalRouter();
  const { t, language } = useLanguage();
  const [searchParams] = useUniversalSearchParams();
  
  const [selectedPackage, setSelectedPackage] = useState<CreditPackage | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [isProcessing, setIsProcessing] = useState(false);
  
  const planId = searchParams.get("plan");

  useEffect(() => {
    const loadPackage = async () => {
      if (!planId) {
        setIsLoading(false);
        return;
      }

      try {
        const packages = await api.getCreditPackages();
        const pkg = packages.find(p => p.id === planId);
        setSelectedPackage(pkg || null);
      } catch (err) {
        console.error("Failed to load package", err);
      } finally {
        setIsLoading(false);
      }
    };

    loadPackage();
  }, [planId]);

  const handlePayNow = () => {
    setIsProcessing(true);
    setTimeout(() => {
      go(`/checkout-success?plan=${planId}`);
    }, 2000);
  };

  const handleCancel = () => {
    go(`/checkout-cancel?plan=${planId}`);
  };

  const getPackageName = (pkg: CreditPackage) =>
    language === "vn" ? pkg.name_vi : pkg.name_en;

  const formatPrice = (price: number) =>
    new Intl.NumberFormat("vi-VN", {
      style: "currency",
      currency: "VND",
    }).format(price);

  if (isLoading) {
    return (
      <div className="app-container">
        <div style={{ textAlign: "center", padding: "100px", color: "#64748b" }}>
          <Loader2 size={24} className="animate-spin" />
          <p style={{ marginTop: "16px" }}>Loading...</p>
        </div>
      </div>
    );
  }

  if (!planId || !selectedPackage) {
    return (
      <div className="app-container">
        <header className="app-top-bar">
          <div className="top-bar-inner">
            <Link to="/buy" className="top-bar-logo">
              <ArrowLeft size={20} />
              <span>{t("checkout.backToBuy")}</span>
            </Link>
          </div>
        </header>

        <div className="app-bg-glow"></div>

        <main className="app-main">
          <div className="app-content-column" style={{ maxWidth: "600px", textAlign: "center" }}>
            <h1 className="dashboard-title">{t("checkout.missingPlan")}</h1>
            <p style={{ color: "#94a3b8", marginBottom: "32px" }}>
              Please select a plan first.
            </p>
            <Link to="/buy" className="checkout-cta-btn">
              {t("checkout.backToBuy")}
            </Link>
          </div>
        </main>
      </div>
    );
  }

  return (
    <div className="app-container">
      <header className="app-top-bar">
        <div className="top-bar-inner">
          <Link to="/buy" className="top-bar-logo">
            <ArrowLeft size={20} />
            <span>{t("checkout.backToBuy")}</span>
          </Link>
        </div>
      </header>

      <div className="app-bg-glow"></div>

      <main className="app-main">
        <div className="app-content-column" style={{ maxWidth: "600px" }}>
          <div style={{ textAlign: "center", marginBottom: "48px" }}>
            <h1 className="dashboard-title">{t("checkout.title")}</h1>
            <p style={{ color: "#94a3b8", fontSize: "1.1rem" }}>
              Complete your payment to unlock professional AI generation
            </p>
          </div>

          {/* Order Summary Section */}
          <div style={{
            background: "rgba(255, 255, 255, 0.02)",
            border: "1px solid rgba(255, 255, 255, 0.08)",
            borderRadius: "20px",
            padding: "32px",
            marginBottom: "32px"
          }}>
            <h3 style={{ 
              color: "#3b82f6", 
              fontSize: "0.85rem", 
              textTransform: "uppercase", 
              letterSpacing: "0.1em", 
              fontWeight: 800,
              marginBottom: "20px"
            }}>
              Order Summary
            </h3>
            
            <div style={{ marginBottom: "24px" }}>
              <h2 style={{ color: "#fff", fontSize: "1.5rem", fontWeight: 700, marginBottom: "8px" }}>
                {getPackageName(selectedPackage)}
              </h2>
              <p style={{ color: "#94a3b8", fontSize: "0.9rem", marginBottom: "16px" }}>
                Professional AI Image Generation Package
              </p>
              
              <div style={{ display: "flex", flexDirection: "column", gap: "12px" }}>
                <div style={{ display: "flex", alignItems: "center", gap: "10px" }}>
                  <Check size={16} strokeWidth={3} style={{ color: "#10b981" }} />
                  <span style={{ color: "#cbd5e1", fontSize: "0.9rem" }}>
                    {selectedPackage.credits} High-Quality Images
                  </span>
                </div>
                <div style={{ display: "flex", alignItems: "center", gap: "10px" }}>
                  <Check size={16} strokeWidth={3} style={{ color: "#10b981" }} />
                  <span style={{ color: "#cbd5e1", fontSize: "0.9rem" }}>
                    Full GEM Thinking Access
                  </span>
                </div>
                <div style={{ display: "flex", alignItems: "center", gap: "10px" }}>
                  <Check size={16} strokeWidth={3} style={{ color: "#10b981" }} />
                  <span style={{ color: "#cbd5e1", fontSize: "0.9rem" }}>
                    Priority GPU Processing
                  </span>
                </div>
              </div>
            </div>
            
            <div style={{
              borderTop: "1px solid rgba(255, 255, 255, 0.1)",
              paddingTop: "20px",
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center"
            }}>
              <span style={{ color: "#94a3b8", fontSize: "1rem" }}>Total Amount:</span>
              <span style={{ color: "#fff", fontSize: "1.75rem", fontWeight: 800 }}>
                {formatPrice(selectedPackage.price_vnd)}
              </span>
            </div>
          </div>

          {/* Payment Actions */}
          <div style={{ display: "flex", gap: "16px", marginBottom: "32px" }}>
            <button 
              className="checkout-cta-btn" 
              onClick={handlePayNow}
              disabled={isProcessing}
              style={{ 
                flex: 1,
                background: isProcessing ? "#1e293b" : "linear-gradient(135deg, #2563eb, #3b82f6)",
                border: "1px solid rgba(59, 130, 246, 0.3)",
                padding: "18px",
                fontSize: "1.1rem",
                fontWeight: 700
              }}
            >
              {isProcessing ? (
                <>
                  <Loader2 size={16} className="animate-spin" style={{ marginRight: "8px" }} />
                  {t("checkout.processing")}
                </>
              ) : (
                `${t("checkout.payNow")} • ${formatPrice(selectedPackage.price_vnd)}`
              )}
            </button>
          </div>
          
          <div style={{ textAlign: "center", marginBottom: "24px" }}>
            <button 
              onClick={handleCancel}
              disabled={isProcessing}
              style={{
                background: "transparent",
                border: "none",
                color: "#64748b",
                cursor: "pointer",
                fontSize: "0.9rem",
                textDecoration: "underline"
              }}
            >
              {t("checkout.cancel")}
            </button>
          </div>

         
        </div>
      </main>
    </div>
  );
}