// --- HELPERS ---

const delay = (ms: number) => new Promise(resolve => setTimeout(resolve, ms));

// --- EXPORTED API FUNCTIONS ---

export async function getCurrentUser(): Promise<User> {
    await delay(300);
    return { ...currentUser };
}

export async function updateUserLocale(locale: Locale): Promise<User> {
    await delay(200);
    currentUser.locale = locale;
    return { ...currentUser };
}

export async function getUserCredits(): Promise<number> {
    await delay(200);
    return currentUser.image_credits;
}

export async function getGems(): Promise<Gem[]> {
    await delay(400);
    return [...mockGems];
}

export async function getFreeGems(): Promise<Gem[]> {
    await delay(300);
    return mockGems.filter(g => g.is_free);
}

export async function getCreditPackages(): Promise<CreditPackage[]> {
    await delay(300);
    return [...mockPackages];
}

export async function createImageGeneration(_prompt: string, _style?: string, _aspectRatio?: string): Promise<ImageGeneration> {
    await delay(2000); // Simulate rendering time

    if (currentUser.image_credits <= 0) {
        throw new Error('Insufficient credits');
    }

    currentUser.image_credits -= 1;

    const newGen: ImageGeneration = {
        id: `gen-${Date.now()}`,
        image_url: 'https://images.unsplash.com/photo-1618005182384-a83a8bd57fbe?q=80&w=1000&auto=format&fit=crop',
        credits_used: 1,
        status: 'completed'
    };

    return newGen;
}

export async function createCheckout(packageId: string): Promise<{ checkoutUrl: string }> {
    await delay(800);
    const pkg = mockPackages.find(p => p.id === packageId);
    if (!pkg) throw new Error('Invalid package');

    // In a real app, this would be a Stripe session URL
    return { checkoutUrl: `/app?checkout_success=true&credits=${pkg.credits}` };
}
