import { useEffect, useMemo, useState } from "react";

type SetSearchParamsInit =
  | URLSearchParams
  | Record<string, any>
  | ((prev: URLSearchParams) => URLSearchParams | Record<string, any>);

type SetSearchParamsOptions = {
  replace?: boolean;
};

export function useUniversalSearchParams(): [
  URLSearchParams,
  (next: SetSearchParamsInit, options?: SetSearchParamsOptions) => void
] {
  const [params, setParams] = useState<URLSearchParams>(() => {
    if (typeof window === "undefined") return new URLSearchParams();
    return new URLSearchParams(window.location.search);
  });

  useEffect(() => {
    if (typeof window === "undefined") return;

    const sync = () => {
      setParams(new URLSearchParams(window.location.search));
    };

    sync();
    window.addEventListener("popstate", sync);
    window.addEventListener("hashchange", sync);

    return () => {
      window.removeEventListener("popstate", sync);
      window.removeEventListener("hashchange", sync);
    };
  }, []);

  const setSearchParams = (next: SetSearchParamsInit, options?: SetSearchParamsOptions) => {
    if (typeof window === "undefined") return;

    const prev = new URLSearchParams(window.location.search);

    let resolved: URLSearchParams | Record<string, any>;
    if (typeof next === "function") resolved = next(prev);
    else resolved = next;

    let sp: URLSearchParams;

    if (resolved instanceof URLSearchParams) {
      sp = resolved;
    } else {
      sp = new URLSearchParams();
      Object.entries(resolved || {}).forEach(([k, v]) => {
        if (v === undefined || v === null) return;
        sp.set(k, String(v));
      });
    }

    const qs = sp.toString();
    const newUrl = `${window.location.pathname}${qs ? `?${qs}` : ""}${window.location.hash || ""}`;

    if (options?.replace) {
      window.history.replaceState({}, "", newUrl);
    } else {
      window.history.pushState({}, "", newUrl);
    }

    setParams(new URLSearchParams(window.location.search));
  };

  return useMemo(() => [params, setSearchParams], [params]);
}
